:root {
  --button-bg-color: #002b68ab;
  --button-hover-bg-color: #f17508ab;
  --button-active-bg-color: #eaba54;
  --light-area: #f4fbff;
  --span-color: #add3f7bb;
}

@font-face {
  font-family: Quicksand;
  src: url("Quicksand-VariableFont_wght.ce1d45b9.ttf") format("truetype");
  font-style: normal;
}

body, html {
  margin: 0;
  padding: 0;
}

html {
  background-image: url("Backgroudn_AluraGeek.231d5497.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

body {
  box-sizing: border-box;
  background-color: var(--cor-de-fundo);
  width: 100%;
  font-family: Raleway;
  line-height: 1;
  position: relative;
  overflow: visible;
}

.header {
  background-image: url("Banner_AluraGeek.3c3d22cf.png");
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 25%;
  padding-top: 200px;
}

.title {
  color: var(--light-area);
  text-transform: uppercase;
  letter-spacing: 5px;
  text-shadow: 0 0 8px var(--light-area);
  width: 100%;
  margin-bottom: 10px;
  font-family: Zen Dots, sans-serif;
  font-size: 90%;
}

.form_productos {
  -webkit-backdrop-filter: blur(10px);
  background: linear-gradient(135deg, #ffffff1a, #fff0);
  border: 1px solid #d490232e;
  border-radius: 20px;
  padding: 20px;
  font-family: Quicksand;
  box-shadow: 0 8px 32px #0000005e;
}

.form-agregar-prod {
  width: 300px;
  margin-left: 10px;
}

.form-input {
  border: 1px solid var(--light-area);
  color: var(--light-area);
  letter-spacing: 2px;
  background: #81b4f21a;
  border: 1px solid #d490232e;
  border-radius: 20px;
  outline: none;
  margin: 7px 0;
  box-shadow: 0 8px 32px #0000005e;
}

.campo__etiqueta, .titulo-producto {
  color: var(--light-area);
  letter-spacing: 5px;
  font-weight: 800;
  line-height: 20px;
}

span {
  color: var(--span-color);
}

button {
  letter-spacing: 2px;
  background-color: var(--button-bg-color);
  color: var(--light-area);
  cursor: pointer;
  border: none;
  border-radius: 4px;
  margin: 15px 10px 10px;
  padding: 10px 20px;
  font-family: Quicksand;
  transition: background-color .3s, transform .3s;
}

button:hover {
  background-color: var(--button-hover-bg-color);
  transform: scale(1.05);
}

button:active {
  background-color: var(--button-active-bg-color);
  transform: scale(.95);
}

.mis_productos {
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  justify-content: center;
  gap: 20px;
  margin-top: 25px;
  display: grid;
}

.productosItem {
  -webkit-backdrop-filter: blur(10px);
  box-sizing: border-box;
  text-align: center;
  background: linear-gradient(135deg, #ffffff40, #fff0);
  border: 1px solid #d490236d;
  border-radius: 20px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 200px;
  padding: 15px 8px;
  display: flex;
  box-shadow: 0 8px 32px #000;
}

.imagen-producto {
  border: 1px solid #d490236d;
  border-radius: 20px;
  width: 100%;
  max-width: 150px;
  height: auto;
  margin: 15px 0;
}

.titulo-producto {
  letter-spacing: 3px;
  word-break: break-word;
  font-weight: 600;
}

.precio-producto {
  color: var(--light-area);
  opacity: .7;
  padding: 10px;
}

.img-borrar {
  align-self: center;
  width: 20px;
  margin: 5px 0 15px;
  transition: transform .5s;
}

.img-borrar:hover {
  transform: scale(1.3);
}

.footer {
  background-image: url("Footer_AluraGeek.e7280905.png");
  background-position: center;
  background-size: cover;
  width: 100%;
  padding: 10px;
}

.footer__rodape {
  letter-spacing: 2px;
  text-align: center;
  color: var(--light-area);
  background-color: #040b2768;
  padding: 30px;
  font-family: Quicksand;
}
